import React, { useState, useEffect, useMemo } from "react";
import axios from "axios";
import {Container, Pagination, Table} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
    const [pageActive, setPageActive] = useState(0)
    const rowsLimit = 4

    const [rows, setRows] = useState([])
    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/rows/?offset=" + (pageActive * rowsLimit)
        }).then(response => {
            setRows(response.data)
        })
    }, [pageActive])
    const pageItems = useMemo(() => {
        const pageLimit = Math.ceil(rows.count / rowsLimit)
        let pageItems = []
        for (let i = 0; i < pageLimit; i++) {
            pageItems.push(
                <Pagination.Item key={i} active={i === pageActive} onClick={() => setPageActive(i)}>
                    {i + 1}
                </Pagination.Item>
            )
        }
        return pageItems
    }, [pageActive, rows.length])

    return(
        <Container>
            <div className="App">
                <h1>Table</h1>
                <Table striped bordered variant="dark">
                    <thead>
                        <tr>
                            <th>Дата</th>
                            <th>Название</th>
                            <th>Количество</th>
                            <th>Расстояние</th>
                        </tr>
                    </thead>
                    <tbody>
                    {rows.length !== 0 ? rows.results.map(row => (
                        <tr key={row.id}>
                            <td>{row.date}</td>
                            <td>{row.title}</td>
                            <td>{row.count}</td>
                            <td>{row.distance}</td>
                        </tr>
                    )): <h4>Данные не найдены.</h4>}
                    </tbody>
                </Table>
            </div>
            <Pagination>
                {pageItems}
            </Pagination>
        </Container>
    );
}

export default App;
